.general-layout {
  min-height: 100vh !important;

  .logo {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 32px;
    margin: 16px;

    color: white;
  }

  .site-layout .site-layout-background {
    background: #fff;
  }

  [data-theme="dark"] .site-layout .site-layout-background {
    background: #141414;
  }

  .trigger {
    padding: 0 24px;
    font-size: 18px;
    line-height: 64px;
    cursor: pointer;
    transition: color 0.3s;
  }

  .site-layout-content {
    padding: 16px;
  }

  .site-layout-footer {
    text-align: center;
  }
}