@import "assets/styles/mixins";

.ticket-create-page {
  position: relative;
  width: 100%;
  font-family: $pgo-txt-font;

  .ticket-header {
    box-shadow: 0 3px 6px -4px rgba(0, 0, 0, .12), -1px 7px 8px 0 rgba(0, 0, 0, .08), 0px 11px 20px 0px rgba(0, 0, 0, .05);
  }

  .ticket-body {

    .ticket-card {
      margin-top: 20px;
      box-shadow: 0 3px 6px -4px rgba(71, 71, 71, 0.12), -1px 7px 8px 0 rgba(75, 75, 75, 0.08), 0px 11px 20px 0px rgba(78, 78, 78, 0.05);

      .ant-card-head-title {
        color: #1890ff;
      }

      .add-fields {
        display: flex;
        justify-content: center;
        width: 100%;
        padding: 10px;

        .add-fields-content {
          width: 70%;
          border-radius: 10px;
          border: 1px solid #c9c9c9;

          .add-fields-header {
            position: relative;
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #c9c9c9;
            padding: 5px 15px 0 15px;

            .pop-up {
              position: absolute;
              top: 30px;
              right: 30px;
              border-radius: 10px;
              border: 1px solid gray;
              padding: 10px;
              width: 240px;
              background-color: white;
              line-height: 18px;
              z-index: 1000;

              &:hover {
                cursor: pointer;
                background-color: #fafafa;
              }
            }
          }

          .add-fields-body {
            padding: 0 15px;
          }
        }
      }

      .member-ship-provider {
        position: relative;
        margin-top: 10px;
        border: 1px solid #c9c9c9;
        border-radius: 5px;
        padding: 20px;

        .provider-label {
          position: absolute;
          top: -8px;
          left: 12px;
          padding: 0 6px;
          color: #1890ff;
          background: white;
          font-size: 12px;
          font-weight: normal;
          pointer-events: none;
          z-index: 2;
        }
      }
    }
  }

  .ticket-footer {
    margin-top: 20px;
  }
}

.ant-select-item {
  font-family: $pgo-txt-font;
  font-size: $sm-font-size !important;
}
