@import "assets/styles/mixins";

.pgo-create-page {
  position: relative;
  width: 100%;
  font-family: $pgo-txt-font;

  .content {
    border-radius: 2px;
    padding: 20px;
    width: 100%;
    background-color: $pgo-tab-bg-color;

    .pgo-progress {
      position: relative;
      display: flex;
      border-radius: 10px;
      height: 20px;
      width: 400px;
      background-color: $pgo-progress-bg-color;

      .pgo-progress-bar {
        position: absolute;
        border-radius: 10px;
        height: 100%;
        background-color: $pgo-progress-bar-color;
      }

      .pgo-progress-amount {
        position: absolute;
        left: 50%;
        color: $pgo-progress-txt-color;
        font-size: $md-font-size;
      }
    }

    .tree-container {
      display: flex;
      width: 100%;
      height: 100%;
      overflow-x: scroll;

      .tree-trunk {
        border-radius: 10px!important;
        min-width: 20px;
        height: 100%;
        min-height: 500px;
        background-color: $pgo-trunk-color;
      }

      .ant-collapse {
        border: none !important;
        background-color: transparent !important;
        font-family: $pgo-txt-font;

        .ant-collapse-item {
          margin-bottom: 100px;
          border: none !important;

          .ant-collapse-header {
            position: relative;
            display: flex;
            width: max-content;
            font-size: $lg-font-size;
            line-height: 25px;
            transition: .5s !important;

            .ant-collapse-arrow {
              padding-top: 3px!important;
            }

            &::after {
              position: absolute;
              top: 0;
              width: 40px;
              height: 25px;
              clear: both;
              content: "";
              z-index: 1000;
            }
          }

          &.node-terming {
            .ant-collapse-header {
              background-color: $pgo-node-terming-bg-color;
              color: $pgo-node-terming-txt-color !important;

              &::after {
                right: -40px;
                border-radius: 0 15px 15px 0;
                background-color: $pgo-node-terming-bg-color;
              }
            }
          }

          &.node-eligibility {
            .ant-collapse-header {
              background-color: $pgo-node-eligibility-bg-color;
              color: $pgo-node-eligibility-txt-color !important;

              &::after {
                left: -40px;
                border-radius: 15px 0 0 15px;
                background-color: $pgo-node-eligibility-bg-color;
              }
            }
          }

          &.node-approval {
            .ant-collapse-header {
              background-color: $pgo-node-approval-bg-color;
              color: $pgo-node-approval-txt-color !important;

              &::after {
                right: -40px;
                border-radius: 0 15px 15px 0;
                background-color: $pgo-node-approval-bg-color;
              }
            }
          }

          &.node-care {
            .ant-collapse-header {
              background-color: $pgo-node-care-bg-color;
              color: $pgo-node-care-txt-color !important;

              &::after {
                left: -40px;
                border-radius: 15px 0 0 15px;
                background-color: $pgo-node-care-bg-color;
              }
            }
          }

          &.node-data {
            .ant-collapse-header {
              background-color: $pgo-node-data-bg-color;
              color: $pgo-node-data-txt-color !important;

              &::after {
                left: -40px;
                border-radius: 15px 0 0 15px;
                background-color: $pgo-node-data-bg-color;
              }
            }
          }

          &.node-receiving {
            .ant-collapse-header {
              background-color: $pgo-node-receiving-bg-color;
              color: $pgo-node-receiving-txt-color !important;

              &::after {
                right: -40px;
                border-radius: 0 15px 15px 0;
                background-color: $pgo-node-receiving-bg-color;
              }
            }
          }

          &.node-grid {
            .ant-collapse-header {
              background-color: $pgo-node-grid-bg-color;
              color: $pgo-node-grid-txt-color !important;

              &::after {
                left: -40px;
                border-radius: 15px 0 0 15px;
                background-color: $pgo-node-grid-bg-color;
              }
            }
          }

          &.node-attachment {
            .ant-collapse-header {
              background-color: $pgo-node-attachment-bg-color;
              color: $pgo-node-attachment-txt-color !important;

              &::after {
                right: -40px;
                border-radius: 0 15px 15px 0;
                background-color: $pgo-node-attachment-bg-color;
              }
            }
          }

          &.node-routing {
            .ant-collapse-header {
              background-color: $pgo-node-routing-bg-color;
              color: $pgo-node-routing-txt-color !important;

              &::after {
                right: -40px;
                border-radius: 0 15px 15px 0;
                background-color: $pgo-node-routing-bg-color;
              }
            }
          }

          .ant-collapse-content {
            border: none;
            width: 100%;
            background-color: transparent !important;

            .ant-collapse-content-box {
              border-bottom-left-radius: 15px;
              border-bottom-right-radius: 15px;
              box-shadow: 0 3px 6px -4px rgba(0, 0, 0, .12), -1px 7px 8px 0 rgba(0, 0, 0, .08), 0px 11px 20px 0px rgba(0, 0, 0, .05);
            }

            .ant-input-affix-wrapper {
              background-color: transparent!important;
            }

            .ant-input {
              font-size: $sm-font-size !important;
              background-color: transparent!important;
            }

            .ant-select {
              font-size: $sm-font-size !important;

              .ant-select-selector {
                background-color: transparent!important;
              }
            }

            .select-red-label {
              .label {
                color: $pgo-select-red-label-color !important;
              }
            }

            .ant-picker {
              background-color: transparent!important;
            }
          }
        }
      }

      .left-tree {
        &.active {
          flex: 0 0 80%;
        }

        .ant-collapse-header {
          justify-content: flex-end;
          margin-left: auto;
          border-radius: 15px 0 0 15px !important;
          padding: 0 15ch !important;

          .ant-collapse-arrow {
            position: absolute;
            top: 0;
            right: 16px !important;
            left: auto !important;
            font-size: $lg-font-size;

            svg {
              transform: rotate(180deg);
            }
          }
        }

        .ant-collapse-content {
          padding: 0 30px 0 0;
        }

        .ant-collapse-item-active {
          .ant-collapse-header {
            border-radius: 15px 0 0 0 !important;
            width: 100%;
          }
        }
      }

      .right-tree {
        padding-top: 50px;

        &.active {
          flex: 0 0 80%;
        }

        .ant-collapse-header {
          margin-right: auto;
          border-radius: 0 15px 15px 0 !important;
          padding: 0 15ch !important;

          .ant-collapse-arrow {
            position: absolute;
            top: 0;
            left: 16px !important;
            font-size: $lg-font-size;
          }
        }

        .ant-collapse-content {
          padding: 0 0 0 30px;
        }

        .ant-collapse-item-active {
          .ant-collapse-header {
            border-radius: 0 15px 0 0 !important;
            width: 100%;
          }
        }
      }

      &.full-width-node {
        .ant-collapse {
          .ant-collapse-item {
            .ant-collapse-header {
              width: 100%;
            }
          }
        }
      }

      &.side-wide-title {
        .left-tree {
          .ant-collapse-header {
            justify-content: flex-start;
          }
        }

        .right-tree {
          .ant-collapse-header {
            justify-content: flex-end;
          }
        }
      }
    }
  }

  .float-label {
    .label-float {
      background: $pgo-tab-bg-color;;
    }
  }
}

.ant-select-item {
  font-family: $pgo-txt-font;
  font-size: $sm-font-size !important;
}
