@import "assets/styles/mixins";

.ticket-create2-page {
  position: relative;
  width: 100%;
  font-family: $pgo-txt-font;

  .ticket-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px 0 20px;
    background-color: #fbfbfb;
    box-shadow: 0 3px 6px -4px rgba(0, 0, 0, .12), -1px 7px 8px 0 rgba(0, 0, 0, .08), 0px 11px 20px 0px rgba(0, 0, 0, .05);

    .header-info {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }

  .ticket-content {
    .content-layer {
      display: flex;
      justify-content: center;
      margin-top: 15px;
      box-shadow: -1px 2px 3px 1px rgba(75, 75, 75, 0.08);
      width: 100%;
      font-size: 15px;
      font-weight: 500;

      .content-grid {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
      }

      &.center-layer {
        flex-direction: column;
        justify-content: space-around;
        width: 50%;

        .ticket-card {
          background-color: #fbfbfb;

          .ant-card-head-title {
            font-size: 20px;
          }
        }
      }

      .ticket-item {
        flex-grow: 1;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        grid-gap: 20px 0;

        &.grid-1-3 {
          grid-template-columns: 1fr 3fr;
        }

        .ticket-label {
          margin-right: 10px;
          font-size: 12px;
          text-align: right;
        }
      }

      .ticket-card {

        .ant-card-head-title {
          //color: #1890ff;
          color: #040404;
          font-weight: 600;
          font-size: 16px;
        }

        .add-fields {
          display: flex;
          justify-content: center;
          width: 100%;
          padding-top: 10px;

          .add-fields-content {
            width: 100%;
            border-radius: 10px;
            border: 1px solid #c9c9c9;

            .add-fields-header {
              position: relative;
              display: flex;
              justify-content: space-between;
              border-bottom: 1px solid #c9c9c9;
              padding: 5px 15px 0 15px;

              .pop-up {
                position: absolute;
                top: 30px;
                right: 30px;
                border-radius: 10px;
                border: 1px solid gray;
                padding: 10px;
                width: 240px;
                background-color: white;
                line-height: 18px;
                z-index: 1000;

                &:hover {
                  cursor: pointer;
                  background-color: #fafafa;
                }
              }
            }

            .add-fields-body {
              padding: 10px 15px;
            }
          }
        }

        .member-ship-provider {
          position: relative;
          margin-top: 10px;
          border: 1px solid #c9c9c9;
          border-radius: 5px;
          padding: 20px;

          .provider-label {
            position: absolute;
            top: -12px;
            left: 12px;
            padding: 0 6px;
            color: #040404;
            font-weight: 600;
            font-size: 16px;
            background: #fbfbfb;
            pointer-events: none;
            z-index: 2;
          }
        }
      }
    }

    .center-layer-wrapper {
      display: flex;
      justify-content: center;
      width: 100%;
    }
  }

  .ticket-footer {
    margin-top: 20px;
  }
}
