@import "assets/styles/mixins";

.profile-page {
  position: relative;
  width: 100%;
  font-family: $pgo-txt-font;

  .content {
    padding: 10px;
    width: 100%;

    .profile-item {
      display: flex;
      align-items: flex-start;
      margin-bottom: 10px;
      font-weight: $md-font-weight;


      .item-label {
        margin-right: 20px;
        color: $pgo-trunk-color;
        font-size: $md-font-size;
      }

      .item-text {
        font-size: $lg-font-size;
      }

      .item-content {
        display: flex;
        flex-direction: column;
      }
    }
  }
}
