@import "assets/styles/mixins";

.pgo-new-page {
  position: relative;
  width: 100%;
  font-family: $pgo-txt-font;

  .header {
    display: flex;
    align-items: center;

    h4 {
      margin-bottom: 0;
    }
  }

  .content {
    margin-top: 20px;
    border-radius: 2px;
    width: 100%;

    .span-1 {
      padding: 10px 0;
      width: 10%;
      text-align: center;
    }

    .span-2 {
      border-left: 1px solid #c4c4c4;
      border-right: 1px solid #c4c4c4;
      padding: 10px 0;
      width: 60%;
      text-align: center;
    }

    .span-3 {
      padding: 10px 0;
      width: 30%;
      text-align: center;
    }

    .tbl-header {
      border: 1px solid #d2d2d2;
      background-color: #d2d2d2;
    }

    .tbl-body {
      border: 1px solid #d2d2d2;
      tr:nth-child(even) {
        background-color: #f1f1f1;
      }

      .submit-btn {
        color: #fff;
        background: #1890ff;
        border-color: #1890ff;

        &:hover {
          background: #40a9ff;
          border-color: #40a9ff;
        }
      }

      .icon:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }
  }
}
