@import "assets/styles/mixins";

.float-label {
  position: relative;
  margin-top: 10px;

  .label {
    font-size: 12px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 12px;
    top: 8px;
    transition: 0.2s ease all;

    z-index: 2;
  }

  .label--icon {
    font-size: 11px;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 30px;
    top: 8px;
    transition: 0.2s ease all;
  }

  .label-float {
    top: -8px;
    left: 6px;
    background: white;
    padding: 0 6px;
    font-size: 10px;
  }

  .ant-form-item {
    margin-bottom: 0;

    .ant-form-item-explain {
      padding: 0 12px;
      font-size: 12px;
      min-height: 15px;
    }
  }

  .ant-input {
    padding-top: 6px;
  }

  .ant-picker {
    width: 100%;
  }

  .ant-select .ant-select-selector {
    padding-top: 6px;
  }

  .ant-select.ant-select-single .ant-select-selector {
    padding-top: 2px;
  }
}
