@import "assets/styles/mixins";

.auth-create-page {
  position: relative;
  width: 100%;
  font-family: $pgo-txt-font;

  .content {
    border-radius: 2px;
    padding: 20px;
    width: 100%;
    background-color: $pgo-tab-bg-color;

    .auth-progress {
      position: relative;
      display: flex;
      border-radius: 10px;
      height: 20px;
      width: 400px;
      background-color: $pgo-progress-bg-color;

      .auth-progress-bar {
        position: absolute;
        border-radius: 10px;
        height: 100%;
        background-color: $pgo-progress-bar-color;
      }

      .auth-progress-amount {
        position: absolute;
        left: 50%;
        color: $pgo-progress-txt-color;
        font-size: $md-font-size;
      }
    }

    .tree-container {
      display: flex;
      width: 100%;
      height: 100%;
      overflow-x: scroll;

      .tree-trunk {
        margin: 0 20px;
        min-width: 20px;
        height: 100%;
        min-height: 500px;
        background-color: $pgo-trunk-color;
      }

      .left-tree {
        flex-shrink: 0;
        width: 80%!important;

        .ant-collapse-header {
          .ant-collapse-arrow {
            position: absolute;
            top: 0;
            right: 16px !important;
            left: auto !important;
            font-size: $lg-font-size;

            svg {
              transform: rotate(180deg);
            }
          }
        }
      }

      .right-tree {
        padding-top: 50px;
        flex-shrink: 0;
        width: 80%;

        .ant-collapse-item:first-child {
          margin-bottom: 220px;
        }

        .ant-collapse-header {
          .ant-collapse-arrow {
            position: absolute;
            top: 0;
            left: 16px !important;
            font-size: $lg-font-size;
          }
        }
      }

      .ant-collapse {
        width: 100%;
        background-color: transparent !important;
        font-family: $pgo-txt-font;
        border: none;

        .ant-collapse-item {
          margin-bottom: 80px;
          border: none;

          .ant-collapse-header {
            font-size: $lg-font-size;
            line-height: 25px;
            transition: .5s !important;
          }

          &.node-patient {
            .ant-collapse-header {
              background-color: $hs-node-patient-bg-color;
              color: $hs-node-patient-txt-color !important;
            }
          }

          &.node-referring {
            .ant-collapse-header {
              background-color: $hs-node-referring-bg-color;
              color: $hs-node-referring-txt-color !important;
            }
          }

          &.node-service {
            .ant-collapse-header {
              background-color: $hs-node-service-bg-color;
              color: $hs-node-service-txt-color !important;
            }
          }

          &.node-care {
            .ant-collapse-header {
              background-color: $hs-node-care-bg-color;
              color: $hs-node-care-txt-color !important;
            }
          }

          &.node-attachment {
            .ant-collapse-header {
              background-color: $hs-node-attachment-bg-color;
              color: $hs-node-attachment-txt-color !important;
            }
          }

          &.node-audit {
            .ant-collapse-header {
              background-color: $hs-node-audit-bg-color;
              color: $hs-node-audit-txt-color !important;
            }
          }

          .ant-collapse-content {
            width: 100%;
            background-color: transparent !important;

            .ant-collapse-content-box {
              border-bottom-left-radius: 15px;
              border-bottom-right-radius: 15px;
              box-shadow: 0 3px 6px -4px rgba(0, 0, 0, .12), -1px 7px 8px 0 rgba(0, 0, 0, .08), 0px 11px 20px 0px rgba(0, 0, 0, .05);
            }

            .ant-input-affix-wrapper {
              background-color: transparent!important;
            }

            .ant-input {
              font-size: $sm-font-size !important;
              background-color: transparent!important;
            }

            .ant-select {
              font-size: $sm-font-size !important;

              .ant-select-selector {
                background-color: transparent!important;
              }
            }

            .select-red-label {
              .label {
                color: $pgo-select-red-label-color !important;
              }
            }

            .ant-picker {
              background-color: transparent!important;
            }
          }
        }
      }
    }
  }

  .float-label {
    .label-float {
      background: $pgo-tab-bg-color;;
    }
  }
}

.ant-select-item {
  font-family: $pgo-txt-font;
  font-size: $sm-font-size !important;
}
