@import "assets/styles/mixins";

.ticket-review-page {
  position: relative;
  width: 100%;
  font-family: $pgo-txt-font;

  .review-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    background-color: #fbfbfb;
    box-shadow: 0 3px 6px -4px rgba(0, 0, 0, .12), -1px 7px 8px 0 rgba(0, 0, 0, .08), 0px 11px 20px 0px rgba(0, 0, 0, .05);

    .header-info {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }

  .review-content {
    .content-layer {
      display: flex;
      justify-content: space-between;
      margin-top: 15px;
      box-shadow: -1px 2px 3px 1px rgba(75, 75, 75, 0.08);
      padding: 10px 0;
      width: 100%;
      font-size: 15px;
      font-weight: 500;

      &.center-layer {
        flex-direction: column;
        justify-content: center;
        width: 50%;
        //background-color: #f0f0f0;
        background: #fbfbfb;
      }

      .review-item {
        flex-grow: 1;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;

        .review-label {
          margin-right: 10px;
          font-size: 12px;
          text-align: right;
        }
      }
    }

    .center-layer-wrapper {
      display: flex;
      justify-content: center;
      width: 100%;
    }
  }
}
