@import "assets/styles/mixins";

.provider-search {
  position: relative;
  width: 100%;
  height: 100%;
  font-family: $pgo-txt-font;
  box-shadow: 0 3px 6px -4px rgba(0, 0, 0, .12), -1px 7px 8px 0 rgba(0, 0, 0, .08), 0px 11px 20px 0px rgba(0, 0, 0, .05);

  .btn-wrapper {
    display: flex;
    align-items: center;
    margin-top: 10px;
    width: 100%;

    .provider-search-btn {
      display: flex;
      justify-content: center;
      border: 1.5px solid $provider-search-btn-border-color;
      border-radius: 25px;
      padding: 5px 20px;
      width: 100%;
      color: white;
      background-color: $provider-search-btn-color;
      font-size: $sm-font-size;

      &:hover {
        cursor: pointer;
        background-color: $provider-search-btn-border-color;
      }
    }
  }
}
